.page {
  height: 100%;
}

.page__body {
  background-color: @color-bg;
  color: @color-text;
  font-size: 15px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  min-width: 320px;
  margin: 0;
  position: relative;

  @media (min-width: @tablet-width) {
    padding-top: 88px;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 88px;
      width: 100%;
      border-bottom: 1px solid @color-lines;
    }
  }
}

.page__main {
  padding-top: 73px;

  @media (min-width: @tablet-width) {
    padding-top: 0;
  }
}
